.card {
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  width: 198px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  background-size: contain;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__item {
  text-align: left;
  font-size: 12px;
}

.card__item-name {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__item-product-code {
  color: #616070;
  align-self: flex-start;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  gap: 80px;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.review {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  margin-bottom: 24px;
  display: flex;
}

.review__stars {
  justify-content: space-between;
  width: 96px;
  display: flex;
}

.review__stars-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.review__stars-star:nth-of-type(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.review__stars-star:last-child {
  margin-right: 0;
}

.review__amount {
  text-align: right;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

* {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

